.home{
    display: flex;
    flex-direction: column;
    height: 100vh;
}
.home_header{
    display: flex;
    justify-content: space-between;
    padding: 20px 30px;
    align-items: center;
}
.home_header a{
    margin: 20px;
    text-decoration: inherit;
    color:black;
    font-size: 20px;
}
.home_header a:hover{
    text-decoration: underline;
}
.home_headerRight{
    display: flex;
    align-items: center;
    min-width: 13vw;
    justify-content: space-between;
}
.home_headerRight > .MuiSvgIcon-root{
    margin-right: 20px;
}
.home_body{
    flex: 1;
    display: flex;
    margin-top: 8%;
    flex-direction: column;
}
.home_body>img{
    object-fit: contain;
    height: 100px;
    
}
